.StoriesSliderWrapper {
  display: flex;
  align-items: stretch;
  position: absolute;
  transition: transform 300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.StoriesWrapper {
  height: 165px;
  position: relative;
  overflow: hidden;
  margin: 0 30px;
}

.StoriesSlider {
  position: relative;
  width: 100%;
  margin: 0 auto;
}